import { Component, OnInit,Inject } from '@angular/core';
import { FormControl } from '@angular/forms';
import { Observable } from 'rxjs/Observable';
import {startWith} from 'rxjs/operators/startWith';
import {map} from 'rxjs/operators/map';
import {MatDialog, MatDialogRef, MAT_DIALOG_DATA} from '@angular/material';

@Component({
  selector: 'app-sell',
  templateUrl: './sell.component.html',
  styleUrls: ['./sell.component.scss']
})
export class SellComponent implements OnInit {
  cities: Array<string> = ['Islamabad', 'Karachi', 'Lahore', 'Rawalpindi'];
  filteredCities = this.cities;
  regFilteredCities = this.cities;
  areas = {"Karachi": ["Korangi", "Defence", "FB Area", "Nazimabad"],
              "Islamabad": ["F7", "F6", "F8", "F9", "F10", "F11", "G7", "G6", "G8", "G9", "G10", "G11","I7", "I6", "I8", "I9", "I10", "I11"]};
  filteredAreas = [];
  citySelected = false;
  color = "";
  modelNos = [];
  constructor(public dialog: MatDialog) { }

  ngOnInit() {
    this.generateYears();
  }

  filter(val: string, array): string[] {
    return array.filter(option =>
      option.toLowerCase().indexOf(val.toLowerCase()) === 0);
  }
  showArea(city){
    this.filteredAreas = this.areas[city];
    this.citySelected = true;
  }
  generateYears(){
    var maxYear: number = (new Date()).getFullYear();
    var minYear: number = maxYear - 50;
    for(maxYear; maxYear > minYear; maxYear--){
      this.modelNos.push(maxYear);
    }
  }
  openDialog(): void {
    let dialogRef = this.dialog.open(DialogColorChoser, {
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed ' , result) ;
      this.color = result;
    });
  }
}


@Component({
  selector: 'color-choser',
  templateUrl: 'color-choser.html',
  styleUrls: ['color-choser.scss']
})
export class DialogColorChoser {
  constructor(
    public dialogRef: MatDialogRef<DialogColorChoser>) { }
}


